import {
    Input, Col, Row, Select, InputNumber, DatePicker, AutoComplete, Cascader, Tooltip, Icon, Radio, Divider
  } from 'antd';
import React, { Component } from 'react';

import './Personal.css'
const InputGroup = Input.Group;
const Option = Select.Option;


class PersonalInfo extends Component {
  state = {
    cinsiyet : "erkek",
    level : "lisans"
  }
  handleChangeLevel = (value) => {
    this.setState({
      level : value
    })
  }

  handleChangeCinsiyet = (value) => {
    this.setState({
      cinsiyet : value.target.value
    })
  }

  render() {
    
    return (
      <div>
        <Divider orientation="left"> Kişisel Bilgiler</Divider>
        <table >
          <tbody>
            <tr>
              <td>
                Cinsiyet
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <Radio.Group value={this.state.cinsiyet} onChange={this.handleChangeCinsiyet}>
                    <Radio value="erkek">Erkek</Radio>
                    <Radio value="kadın">Kadın</Radio>
                  </Radio.Group>
                </InputGroup>
              </td>
            </tr>
            <tr >
              <td>
                Mezuniyet Yılı
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <InputNumber style={{width:300}}/>
                </InputGroup>
              </td>
            </tr>
            <tr >
              <td>
                Eğitim Durumu
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <Select placeholder="Öğrenim durumunu seçiniz" style={{width:300}} onChange={this.handleChangeLevel}>
                    <Select.Option key="lisans">Lisans</Select.Option>
                    <Select.Option key="uögrenci">Uzmanlık Öğrencisi</Select.Option>
                    <Select.Option key="uzman">Uzman</Select.Option>
                  </Select>
                  {this.state.level === "uzman" ? <InputNumber placeholder="Uzmanlık Yılını Giriniz" style={{width:200}}/> : null}
                  {this.state.level === "uzman" ? 
                    <Select placeholder="Uzmanlık Alanınızı seçiniz" style={{width:300}} >
                      <Select.Option key="adcc">Ağız Diş ve Çene Cerrahisi</Select.Option>
                      <Select.Option key="adcr">Ağız Diş ve Çene Radyolojisi</Select.Option>
                      <Select.Option key="cdh">Çocuk Diş Hekimliği</Select.Option>
                      <Select.Option key="end">Endodonti</Select.Option>
                      <Select.Option key="ort">Ortodonti</Select.Option>
                      <Select.Option key="per">Periodontoloji</Select.Option>
                      <Select.Option key="rdt">Restoratif Diş Tedavisi</Select.Option>
                      <Select.Option key="pdt">Protetik Diş Tedavisi</Select.Option>
                    </Select> 
                  : null}
                </InputGroup>
              </td>
            </tr>
            <tr >
              <td>
                Çalıştığı Kurum
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <Select placeholder="Öğrenim durumunu seçiniz" style={{width:300}}>
                    <Select.Option key="üniversite">Lisans</Select.Option>
                    <Select.Option key="opol">Özel Poliklinik</Select.Option>
                    <Select.Option key="omua">Özel Muayenehane</Select.Option>
                    <Select.Option key="devlet">Devlet Hastanesi</Select.Option>
                    <Select.Option key="ağız">Ağız ve Diş Sağlığı Merkezi-Hastanesi</Select.Option>
                  </Select>
                </InputGroup>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default PersonalInfo;